@import url("https://fonts.googleapis.com/css?family=Roboto:100,300,400,700,900");
*, html {
  font-family: 'Roboto', sans-serif; }

@media (min-width: 1200px) {
  .container {
    max-width: 960px; }
  .wrapper {
    max-width: 768px;
    margin: auto; } }

@media (min-width: 1200px) and (min-width: 992px) {
  .modals > .propertyProModal .modal-body img {
    padding: 1.5em;
    width: 33%; } }

@media (min-width: 992px) and (max-width: 1199.98px) {
  .modals > .propertyProModal .modal-body img {
    padding: 1.5em;
    width: 33%; } }

@media (min-width: 768px) and (max-width: 991.98px) {
  .modals > .propertyProModal .modal-body img {
    padding: 1.5em;
    width: 50%; } }

@media (min-width: 576px) and (max-width: 767.98px) {
  .modals > .propertyProModal .modal-body img {
    padding: 1.5em;
    width: 50%; } }

.up {
  border-radius: 50%;
  background-color: #070707;
  padding: 10px;
  color: #fff; }

.z-index {
  z-index: 1; }

header {
  padding: 50px 0 0 0; }
  header li {
    align-self: center; }
  header ul li a span {
    color: #58595a; }
    header ul li a span:hover {
      color: #070707;
      border-bottom: 1px solid #070707;
      padding-bottom: 10px;
      transition: .2s; }

h1 {
  font-size: 2.875rem; }

h2 {
  font-size: 2.25rem; }

h3 {
  font-size: 1.875rem; }

h1, h3 {
  font-weight: 900; }

section {
  margin-bottom: 4rem; }

section.hero {
  margin-bottom: 0;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center; }
  section.hero h1 {
    margin-bottom: 50px; }
  section.hero h2 {
    font-weight: 100; }

.modals > .propertyProModal .modal-body,
.modals > .lamudiModal .modal-body,
.modals > .mypropertyModal .modal-body,
.modals > .xendModal .modal-body,
.modals > .simpliphpyModal .modal-body,
.modals > .globeModal .modal-body {
  padding-bottom: 3em; }

.modals > .propertyProModal .modal-content,
.modals > .lamudiModal .modal-content,
.modals > .mypropertyModal .modal-content,
.modals > .xendModal .modal-content,
.modals > .simpliphpyModal .modal-content,
.modals > .globeModal .modal-content {
  border: none;
  border-radius: 0;
  margin: 0; }
  .modals > .propertyProModal .modal-content .close,
  .modals > .lamudiModal .modal-content .close,
  .modals > .mypropertyModal .modal-content .close,
  .modals > .xendModal .modal-content .close,
  .modals > .simpliphpyModal .modal-content .close,
  .modals > .globeModal .modal-content .close {
    opacity: 1;
    outline: none; }

.modals > .propertyProModal .modal-dialog,
.modals > .lamudiModal .modal-dialog,
.modals > .mypropertyModal .modal-dialog,
.modals > .xendModal .modal-dialog,
.modals > .simpliphpyModal .modal-dialog,
.modals > .globeModal .modal-dialog {
  max-width: 100%;
  margin: 0; }

.modals > .propertyProModal .modal-body,
.modals > .lamudiModal .modal-body,
.modals > .mypropertyModal .modal-body,
.modals > .xendModal .modal-body,
.modals > .simpliphpyModal .modal-body,
.modals > .globeModal .modal-body {
  max-height: 90vh;
  overflow-y: auto; }

.modals > .propertyProModal .modal-back,
.modals > .lamudiModal .modal-back,
.modals > .mypropertyModal .modal-back,
.modals > .xendModal .modal-back,
.modals > .simpliphpyModal .modal-back,
.modals > .globeModal .modal-back {
  width: 16px; }

.modals > .propertyProModal p,
.modals > .lamudiModal p,
.modals > .mypropertyModal p,
.modals > .xendModal p,
.modals > .simpliphpyModal p,
.modals > .globeModal p {
  padding: 1.5em; }

.modals > .propertyProModal h5,
.modals > .lamudiModal h5,
.modals > .mypropertyModal h5,
.modals > .xendModal h5,
.modals > .simpliphpyModal h5,
.modals > .globeModal h5 {
  font-weight: 700; }

.modals > .propertyProModal .modal-body img {
  padding: 1.5em;
  max-width: 100%;
  height: auto; }

.modals > .lamudiModal ul > li {
  list-style-type: none;
  text-align: left;
  padding: 0; }
  .modals > .lamudiModal ul > li a {
    font-size: 11px; }

.exp h1, .craft h1, .contact h1 {
  margin-bottom: 3.75rem; }

.exp h3 {
  text-transform: capitalize;
  margin: 0;
  font-weight: 700; }

.exp p {
  font-weight: 900;
  color: #58595a;
  text-transform: uppercase;
  letter-spacing: 3px; }
  .exp p.date {
    font-size: .75rem;
    margin-bottom: 2rem; }
  .exp p.company {
    font-size: 1rem; }

.exp hr {
  margin-bottom: 2rem; }

.craft .card {
  height: 200px;
  line-height: 200px;
  margin-bottom: 20px;
  border: 0;
  border-radius: 0;
  background-color: #f0f0f0; }

.craft .size-def {
  width: 30%; }

.contact h2 {
  font-weight: 100; }

.contact .contacts {
  font-weight: 400;
  font-size: 16px;
  padding-top: 50px;
  line-height: 2; }

.contact ul li, .footer ul li {
  list-style-type: none; }

.footer ul li {
  display: inline;
  padding-right: 10px; }

.footer ul li a {
  color: #58595a;
  text-decoration: none !important; }
  .footer ul li a :hover {
    text-decoration: none !important;
    color: #070707;
    outline: none;
    transition: .2s; }

body {
  margin: 0;
  padding: 0;
  background-color: #f6f9fc; }
